import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material';

@Component({
  selector: 'lc-dialog-alert',
  template: `<h2 mat-dialog-title>{{title}}</h2>

<mat-dialog-content>
  <p class="text-center"[innerHTML]="message"></p>
</mat-dialog-content>

<mat-dialog-actions>
  <button mat-raised-button (click)="close()">OK</button>
</mat-dialog-actions>`,
  styles: [``]
})
export class DialogAlertComponent implements OnInit {

  public title;
  public message;

  constructor(private dialogRef: MatDialogRef<DialogAlertComponent>) { }

  ngOnInit() {
  }

  close() {
    this.dialogRef.close();
  }

}
